.item {
  font-size: 36px;
  position: relative;
}
.item:after {
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    border-bottom: 1px solid blue;
}
.border {
  border-bottom: solid 1px red;
  margin-bottom: 10px;
}
.item-2 {
  font-size: 36px;
  margin: 0.1rem 25%;
  width: 6rem;
  position: relative;
}
.item-2:after {
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    border: 1px solid #000;
    border-radius: 0;
}
.item-div {
  height: 1px;
  width: 100%;
  background: blue;
  -webkit-transform: scaleY(0.1);
          transform: scaleY(0.1);
}
